import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router, Routes, Route } from "@angular/router";
import { Subscription } from "rxjs/Rx";
import { FormArray, FormGroup, FormControl, Validators, FormBuilder, ReactiveFormsModule } from "@angular/forms";
import { RecipeService } from "../recipe.service";
import { Recipe } from '../recipe';

@Component({
  selector: 'rb-recipe-edit',
  templateUrl: './recipe-edit.component.html',
  styles: []
})
export class RecipeEditComponent implements OnInit, OnDestroy {
	recipeForm: FormGroup;
	private recipeIndex: number;
	private recipe: Recipe;
	private isNew = true;
	private subscription: Subscription;

  constructor(private route: ActivatedRoute, 
  			  private recipeService: RecipeService,
  			  private formBuilder: FormBuilder,
          private router: Router) { }

  ngOnInit() {
  	this.subscription = this.route.params.subscribe(
  		(params: any) => {
  			if(params.hasOwnProperty('id')){
  				this.isNew = false;
  				this.recipeIndex = +params['id'];
  				this.recipe = this.recipeService.getRecipe(this.recipeIndex);
  			} else{
  				this.isNew = true;
  				this.recipe = null;
  			}
        this.initForm();
  		}
  		);
  }

  onAddItem(name: string, amount: string){
    (<FormArray>this.recipeForm.controls['ingredients']).push(
      new FormGroup({
            name: new FormControl(name, Validators.required),
            amount: new FormControl(amount, [
              Validators.required,
              Validators.pattern("\\d+")
              ])
          })
      );
  }

  onRemoveItem(index: number){
    (<FormArray>this.recipeForm.controls['ingredients']).removeAt(index);
  }

  onSubmit(){
  const newRecipe = this.recipeForm.value;
  if(this.isNew){
    this.recipeService.addRecipe(newRecipe);
  }else{
    this.recipeService.editRecipe(this.recipe, newRecipe);
  }
  this.navigateBack();
  }

  ngOnDestroy(){
  	this.subscription.unsubscribe();
  }

  onCancel(){
    this.navigateBack();
  }

  private navigateBack(){
  this.router.navigate(['../']);
  }

  private initForm(){
  	let recipeName = '';
  	let recipeImageUrl = '';
  	let recipeContent = '';
  	let recipeIngredients: FormArray = new FormArray([]);
    let recipeCedula = '';
    let recipeDireccion = '';
    let recipeTelefono = '';
    let recipeAlergias= '';
    let recipeEdad = '';
    let recipeSexo = '';
    let recipeRecipeImageUrl = '';

  	if(!this.isNew){
      if(this.recipe.hasOwnProperty('ingredients')){
        for(let i=0; i<this.recipe.ingredients.length; i++){
        recipeIngredients.push(
          new FormGroup({
            name: new FormControl(this.recipe.ingredients[i].name, Validators.required),
            amount: new FormControl(this.recipe.ingredients[i].amount, [
              Validators.required,
              Validators.pattern("\\d+")
              ])
          })
          );
      }
      }		
  		recipeName = this.recipe.name;
  		recipeImageUrl = this.recipe.imagePath;
  		recipeContent = this.recipe.description;
      recipeCedula = this.recipe.cedula;
      recipeDireccion = this.recipe.direccion;
      recipeTelefono = this.recipe.telefono;
      recipeAlergias = this.recipe.alergias;
      recipeSexo = this.recipe.sexo;
      recipeEdad = this.recipe.edad;
      recipeRecipeImageUrl = this.recipe.imageRecipePath;
  	}
  	this.recipeForm = this.formBuilder.group({
  			name: [recipeName, Validators.required],
  			imagePath: [recipeImageUrl, Validators.required],
  			description: [recipeContent, Validators.required],
        cedula: [recipeCedula, Validators.required],
        direccion: [recipeDireccion, Validators.required],
        telefono: [recipeTelefono, Validators.required],
        alergias: [recipeAlergias, Validators.required],
        sexo: [recipeSexo, Validators.required],
        edad: [recipeEdad, Validators.required],
        imageRecipePath: [recipeRecipeImageUrl, Validators.required],
  			ingredients: recipeIngredients
  		});
  }
}



