import { Injectable, EventEmitter } from '@angular/core';
import { Recipe } from './recipe';
import { Ingredient} from'../ingredient';
import { Headers, Http, Response, HttpModule } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class RecipeService {
  recipesChange = new EventEmitter<Recipe[]>();

	private recipes: Recipe[]=[
		new Recipe('Maria Andreina Cane','El paciente presenta fiebre constante debido a una gripe no atendida a tiempo','https://media.licdn.com/mpr/mpr/shrinknp_200_200/AAEAAQAAAAAAAAetAAAAJDUzMWM2ZjJiLTE4MTgtNGZhZS05MjkyLTg5MDNjY2IxMjJmMA.jpg',[
			new Ingredient('Atamel (Caja de 8 Pastillas)',2),
			new Ingredient('Loratadina (Caja de 32 pastillas)',1)
			], '20.514.996','Naguanagua Edo Carabobo','04129621110','No Presenta','Femenino','24 Años','http://www.farmatodo.com.ve/portals/0/Salud/Imagenes/recipesmedico.gif'),
		new Recipe('Scarlett Johansson','El paciente no presenta enfermedad, vino por un chequeo anual','https://pbs.twimg.com/profile_images/714633747770044416/Qu8c4fje.jpg',[
      new Ingredient('Loratadina (Caja de 32 pastillas)',1)
      ], 'No Es Venezolana','Estados Unidos El Imperio','No Posee','No Presenta','Femenino','32 Años','http://www.farmatodo.com.ve/portals/0/Salud/Imagenes/recipesmedico.gif')
	];

  constructor(private http: Http) { }

  getRecipes(){
  	return this.recipes;
  }

  getRecipe(id: number){
  	return this.recipes[id];
  }

  deleteRecipe(recipe: Recipe){
  	this.recipes.splice(this.recipes.indexOf(recipe), 1);
  }

  addRecipe(recipe: Recipe){
  this.recipes.push(recipe);
  }

  editRecipe(oldRecipe: Recipe, newRecipe: Recipe){
    this.recipes[this.recipes.indexOf(oldRecipe)] = newRecipe;
  }

  storeData(){
    const body = JSON.stringify(this.recipes);
    const headers = new Headers({
      'Content-Type': 'application/json' //Enviamos data tipo json al servidor
    });
    return this.http.put('https://pruebaangular2-e20d1.firebaseio.com/pacientes.json', body, { headers: headers});
  }

  fetchData(){
     return this.http.get('https://pruebaangular2-e20d1.firebaseio.com/pacientes.json').map((response:Response) => response.json()).subscribe(
       (data: Recipe[]) => {
         this.recipes = data;
         this.recipesChange.emit(this.recipes);
       });
  }

}



